.wrapper
{
    clear: both;
    width: 100%;
}

/* clear the contents after wrapper.. works better than clearing on next item */
.wrapper::after
{
    content: "";
    visibility: hidden;
    height: 0;
    display: block;
    clear: both;
}
.wrapleft
{
    float: left;
    width: 100%;
}

/* this holds the status table */
article
{
    height: auto;
    overflow: auto;
    margin-right: 310px;
    line-height:1em;
}
article table
{
    width: 100%;
    border-collapse: collapse;
}
article table tr
{
    background-color: #000000;
    color: darkorange;
}

/* The aside holds the status indicators */
